*{
    margin: 0px; 
    padding: 0px;
    box-sizing: border-box; 
    
}


.header {
    background-color: black;
    color: white;
    height: 50vh;
    background-image:url("https://images.unsplash.com/photo-1541701494587-cb58502866ab?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2070&q=80");
    background-repeat: no-repeat;
    background-size:cover;
}

.header__name {
    font-size: 200px;
    font-weight: 600;
    font-family: "roboto";
    text-align: center;
}

.underDiv {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: -30px;
    font-family: 'Smooch Sans', sans-serif;
    font-size: 17px;
}

.header__date {
    border-right: 1px solid white;
    margin-right: 10px;
    padding-right: 10px;
}
@media  (max-width:500px) {
    .header{
    height: 10vh;
    background-color: red;
    }
    .header__name{
        font-size: 20px;
        font-weight: 200;
        font-family: "roboto";
        
    }
    .headerText{
        display: flex; 
        justify-content: start;
        
    }
    .underDiv>*{
        display: none; 
    }
    
}

.navBar__container{
    background-color: black;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 10vh;
    color: white;
    border-top: 1px solid rgba(128, 128, 128, 0.616)  ;
    border-bottom: 1px solid rgba(128, 128, 128, 0.616)  ;
    
    padding-left: 10px ;
    padding-right: 10px;
}
.nav__links{
    flex: 2 1; 
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    /* margin-left:20px; */
    margin-right: 100px;
    list-style: none;
    cursor: pointer;
    font-family: 'Smooch Sans', sans-serif;
    font-size: 20px;
    
}
.nav__links>li{
    border-right: 1px solid white ;
    border-left: 1px solid white ;
    padding: 0 20px;
}
.nav__links>li:hover{
    color: rgb(226,18,122); 
}

.social__links{
    flex: 1 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: end;
    margin-right: 50px;
    margin-right: 20px;
    color: rgb(226,18,122);
}
.social__links>*{
    margin-left: 25px;
}
.social__links>*:hover{
    color:white; 
}
.profile{
    flex: 1 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center; 
    width: 20px ;
    
}
.profile>*{
    margin-left: 30px;
}
@media  screen and (max-width: 500px) {
    .profile__icon{
        position: relative; 
        top: 0; 
    }
}

.input__container{
    display: flex;
    position: relative;
    justify-content: center;
    align-items: flex-start;
   
    
    
}
input{
    width: 50%;
    height: 40px; 

   
}
input[type="text"]
{
    font-size:24px;
}
input:focus{
    outline: none; 
}

button{
    background-color:rgb(226,18,122) ;
    width: 60px;
    border: none;
    color: white;
    height: 40px; 
}
.headline__container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    border-width: 2px;
    border-style: solid;
    border-image: linear-gradient(to right, rgb(247,198,38), rgb(234,55,102)) 1;
    margin-top: 10px;
    height: 100vh;
}
.headlinePost__container{
    border-width: 2px;
    border-style: solid;
    border-image: linear-gradient(to right, rgb(247,198,38), rgb(234,55,102)) 1;
    height:50vh;
    background-color: black;
    color: white;
    
    
    
}

.headlinePost__container>img{
    min-width: 100%;
    height: 60%;
    object-fit: cover; 
    

}
.headlinePost__container>p:first-of-type{
    font-size: 25px;
    font-family: Arial, Helvetica, sans-serif;


}
.headlinePost__container>p:first-of-type:hover{
  color: rgb(234,55,102);
}
.headlinePost__container>p:last-of-type{
    font-family: 'Smooch Sans', sans-serif;
    font-size: 30;
    letter-spacing: 1px;
}
.headlinePost__container>*:not(:first-child){
    margin-left: 10px;
}
.blog__author{
    font-size: 16px;
    color:rgb(234,55,102) ;
}
.modal__container{
 background-color: black;
 height: 70px;
 width: 100px;
 display: flex;
 flex-direction: column;
 align-items:flex-start;
 justify-content: center;
 position: absolute;
 right: 0;
 margin-right: 50px;
 /* margin-top: -40px; */
 
}
.modal__container>button{
    border: none;
    background: none;
    color: white;
    margin-bottom: 2px;
    margin-left: 3px;
    
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

.horizontal__line{
    width: 96%;
    border-width: 1px;
    border-style: solid;
    background-color: white ;
    margin-top: 10px;
    margin-bottom: 10px;
}

.container{
    display: flex; 
    flex-direction: column;
    margin-left: 80px;
    margin-top: 30px;
    width: 60%;
    
    
}
.blog{
    display: flex; 
    flex-direction: row;
}
.blogText__container{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    border-top: 1px solid grey;
    /* border-bottom: 1px solid grey; */
    height: 25vh;
    /* margin-top: 10px; */

}
.blogText__container>*{
    margin-left: 10px;
    text-align: start;
}
.blog__details{
    display: flex; 
    flex-direction: row;
    margin-top: 20px;
    
}
.blog__details>*{
    margin-left: 5px;
   
}
.blogText__container>div:first-of-type{
    margin-top: 20px;
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    
}
.blogText__container>div:first-of-type:hover{
    color:rgb(226,18,122); 
    cursor: pointer;
}
.blogText__container>div:last-of-type{
    font-family: Arial, Helvetica, sans-serif;
    color: grey;
    font-size: 13px;
}
img{
    width: 200px;
    height: 125px; 
    object-fit: cover;
}
.author{
    color:rgb(226,18,122);
}
.main{
   display:flex;
   background-color: black;
   height: 40vh ;
   border-top: 1px solid rgba(128, 128, 128, 0.315);
   border-bottom: 1px solid rgba(128, 128, 128, 0.315);
   flex-direction: row;
   justify-content: space-between;
   margin-top: 20px;
   font-family: Arial, Helvetica, sans-serif;
   font-weight: bold;
   padding: 0 20px;
   
    
}
.main>*{
    margin-left: 30px;
}


.subscribe__data{
    display: flex; 
    flex-direction: row;
    justify-content: center;
    
}

.subscribe__data>*{
    margin-left: 20px;
}
.brief__container{
    display: flex; 
    flex: 4 1;
    align-items: start;
    flex-direction: column;
    align-items: flex-start;
    color:grey;
    margin-top: 37px;
    /* width: 50%;  */
    
}
.brief__container:hover{
    color:rgb(226,18,122);
}
.subscribe__text{
    color: grey; 
}
.subscribe__text:hover{
    color: rgb(226,18,122);
}
.subscribe__container{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    flex: 4 1;

}
.subscribe__input{
    border-radius: 25px;
    width: 80%;
    padding-left: 10px;
}
.subscribe__button{
    border-radius: 25px;
    width: 100px;
}
.footer__container{
    display: flex; 
    flex-direction: column;
    justify-content: space-around;
    width: 100%;
    height: 20vh;
    background-color: black;
    color: white;
    padding: 0 20px;
    
}
.footer__row{
    display: flex; 
    flex-direction: row;
    justify-content: space-between;
}
ul{
    display: flex; 
    flex-direction: row;
    justify-content: space-around;
    flex: 6 1;
    
    
}
.list>li{
    list-style: none;
    font-family: 'Smooch Sans', sans-serif;
    font-size: 25px;
    cursor: pointer;
}
.list>li:hover{
    color:rgb(226,18,122);
}
.blog__name{
    flex: 6 1;
    font-family: "roboto";
    font-size: 25px;
}
.footer__links{
    flex: 4 1;
}
.footer__copyright{
    text-align: center;
}
